<template>
    <div>
        <el-button type="primary" @click="onConfirm">导出</el-button>
        <el-input v-model="fileName" placeholder="请输入文件名"></el-input>
    </div>
</template>
<script lang='ts' setup>
import { ref } from 'vue'
import { allUserList, userColumn } from './src/data'
import dayjs from 'dayjs'
const dateFormat = (val: any, format = 'YYYY-MM-DD') => {
    if (isNaN(val)) {
        return val
    }
    val = parseInt(val)
    return dayjs(val).format(format)
}
const fileName = ref('')
/**
 * 导出按钮点击事件
 */
const loading = ref(false)
const onConfirm = async () => {
    loading.value = true
    // const allUser = (await getUserListAll()).list  //获取所有的表格信息
    const allUser = allUserList // 暂定默认的数据模仿接口数据
    // 导入工具包
    const excel: any = await import('@/utils/Export2Excel.js')
    const data = formatJson(userColumn, allUser)
    console.log(data)
    // return
    // // return
    excel.export_json_to_excel({
        // excel 表头
        header: Object.keys(userColumn),
        // excel 数据（二维数组结构）
        data,
        // 文件名称
        filename: fileName.value,
        // 是否自动列宽
        autoWidth: true,
        // 文件类型
        bookType: 'xlsx'
    })
}

// 该方法负责将数组转化成二维数组
const formatJson = (headers: any, rows: any) => {
    console.log(headers, 'hhh')
    console.log(rows, 'rows')
    // 首先遍历数组
    // [{ username: '张三'},{},{}]  => [[’张三'],[],[]]
    return rows.map((item: any) => {
        console.log(Object.keys(headers), 'Object.keys(headers)')
        return Object.keys(headers).map(key => {

            // 时间特殊处理
            if (headers[key] === 'openTime') {
                return dateFormat(item[headers[key]])
            }
            // 角色特殊处理
            if (headers[key] === 'role') {
                const roles = item[headers[key]]

                return JSON.stringify(roles.map((role: any) => role.title))
            }
            return item[headers[key]]
        })
    })
}

</script>
<style scoped></style>